html, body, #root{
  min-height:100vh;
}

p, h1, h2, h3, h4, h5, h6{
  margin:0;
}

.body{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow-x: hidden;
}

.flex-container {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height:90vh;
}

.header{
  -webkit-transition-duration: 300ms;
       -o-transition-duration: 300ms;
          transition-duration: 300ms;
  -ms-flex:0 0 10vh;
      flex:0 0 10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.header.open{
  -webkit-transform: translate(90%);
      -ms-transform: translate(90%);
          transform: translate(90%);
}

.topElement{
  display: none;
}

.main{
  background-color: #fbfbfb;
  -ms-flex-positive:1;
      flex-grow:1;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  -webkit-transition-duration: 300ms;
       -o-transition-duration: 300ms;
          transition-duration: 300ms;
  -ms-flex-align: baseline;
      align-items: baseline;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.main.open{
  -webkit-transform: translate(90%);
      -ms-transform: translate(90%);
          transform: translate(90%);
}

.componentRow{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: stretch;
      align-self: stretch;
}

.nav{
  position:absolute;
  top:0;
  left:-90%;
  height:110vh;
  width: 90%;
  background-color: #222222;
  -webkit-transition: left .3s ease-in-out;
  -o-transition: left .3s ease-in-out;
  transition: left .3s ease-in-out;
}

.nav a{
  color:#999;
  text-decoration: none;
}

.navIcon{
  position: absolute;
  left: 5px;
  color: black;
  text-decoration: none;
  font-size: 1.5em;
}

.nav.open{
  left:0;
}

.navItems{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  list-style: none;
  padding: 0;
  height:100%;
  margin:0;
}

.nav ul li{
  display: -ms-flexbox;
  display: flex;
  padding: 10px 0 0 10px;
}

.subItems{
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-left:20px;
}

@media (min-width: 768px){
  .flex-container{
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .appName{
    -ms-flex:8 1 0%;
        flex:8 1 0%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .topElement{
    display: initial;
    color:#999;
  }

  .main{
    -ms-flex:8 1 0%;
        flex:8 1 0%;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
  }

  .componentContainer{
    margin:10px;
  }

  .navIcon{
    position: inherit;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    -ms-flex:1 1 0%;
        flex:1 1 0%;
    background-color: #222;
    max-width:160px;
  }

  .navIcon h2{
    -ms-flex-item-align: center;
        align-self: center;
  }

  .navIcon a{
    display: none;
  }

  .nav{
    position: inherit;
    bottom: initial;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    width: initial;
    height: 90vh;
    -ms-flex-order:-1;
        order:-1;
    -ms-flex:1 1 0%;
        flex:1 1 0%;
    max-width: 160px;
  }

  .nav ul{
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .nav ul li{
    -ms-flex-item-align: start;
        align-self: flex-start;
    display: initial;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    padding:10px 0 10px 5px;
    -ms-flex-positive:0;
        flex-grow:0;
  }

  .nav ul li:hover{
    background-color: rgba(0,0,0,.5);
    color:white;
  }
}


